export const fadeIn = (element, ms = 100, cb = null) => {
    element.style.transition = `opacity ${ms}ms`;
    element.style.opacity = '0';
    setTimeout(() => {
        element.style.opacity = '1';
    }, 100);
    if (cb) {
        element.addEventListener('transitionend', () => {
            cb.call(this);
        }, { once: true });
    }
};
export const fadeOut = (element, ms = 100, cb = null) => {
    element.style.transition = `opacity ${ms}ms`;
    element.style.opacity = '1';
    setTimeout(() => {
        element.style.opacity = '0';
    }, 100);
    if (cb) {
        element.addEventListener('transitionend', () => {
            cb.call(this);
        }, { once: true });
    }
};
