const axios = require('axios');
export const submitColumnSettings = (data) => {
    let formData = mapDataToFormData({
        action: 'ac-columns',
        id: 'save',
        _ajax_nonce: AC._ajax_nonce,
        data: data
    });
    return axios.post(ajaxurl, formData);
};
const mapDataToFormData = (data, formData = null) => {
    if (!formData) {
        formData = new FormData();
    }
    Object.keys(data).forEach(key => {
        formData.append(key, data[key]);
    });
    return formData;
};
export const _switchColumnType = (type, data) => {
    let formData = mapDataToFormData({
        _ajax_nonce: AC._ajax_nonce,
        action: 'ac-columns',
        id: 'select',
        type: type,
        data: data,
        current_original_columns: JSON.stringify(AdminColumns.Form.getOriginalColumns()),
    });
    return axios.post(ajaxurl, formData);
};
export const switchColumnType = (type, list_screen = AC.list_screen) => {
    let formData = mapDataToFormData({
        _ajax_nonce: AC._ajax_nonce,
        action: 'ac-columns',
        id: 'select',
        type: type,
        list_screen: list_screen,
        current_original_columns: JSON.stringify(AdminColumns.Form.getOriginalColumns().map((e) => e.getName())),
    });
    return axios.post(ajaxurl, formData);
};
export const refreshColumn = (name, data, list_screen = AC.list_screen) => {
    let formData = mapDataToFormData({
        _ajax_nonce: AC._ajax_nonce,
        action: 'ac-columns',
        id: 'refresh',
        column_name: name,
        data: data,
        list_screen: list_screen,
        current_original_columns: JSON.stringify(AdminColumns.Form.getOriginalColumns().map((e) => e.getName())),
    });
    return axios.post(ajaxurl, formData);
};
